<template>
  <div class="Middle">
    <h3>借助中间件通信</h3>
    <p>和pubsub很类似,优点是不需要引用类库,自己新建文件即可使用</p>
    <p>首先新建个工具文件夹,当中间件,名字自己定义</p>
    <pre>
import Vue from 'vue'
export default new Vue()
    </pre>
    <p>这样一个中间件就做好了</p>
    <p>和pubsub使用差不多</p>
    <p>import Bus from "../eventBus.js"</p>
    <button @click="ck">点我试试</button>
    <pre>{{ info }}</pre>
  </div>
</template>

<script>
import Bus from "../eventBus.js";
export default {
  data() {
    return {
      info: `
//定义,如果触发Tabdemo就执行里面的函数 --当前组件中做操作
  mounted() {
    Bus.$on("Tabdemo", this.shijiao);
  },

//其他组件想触发他这样,
Bus.$emit("Tabdemo", {name:"张三",age:18});

//下面是一些其他写法
Bus.$on("Tabdemo", (info) => {
      this.ceshikk = info; //接受传递的东西
      console.log(this.ceshikk);
    });

//传递了函数
 Bus.$emit("Tabdemo", function () {
        console.log("嘿嘿嘿");
        return "哈哈哈";
      });

Bus.$on("Tabdemo", (info) => {
      info();
      var a = info();
      console.log(a);
    });

      `,
    };
  },
  mounted() {
    Bus.$on("Tabdemo", (info) => {
      info();
      var a = info();
      console.log(a);
    });
  },
  methods: {
    ck() {
      Bus.$emit("Tabdemo", function () {
        //console.log("嘿嘿嘿");
        return "哈哈哈";
      });
    },
    ceshi(info) {
      alert("我被触发了");
      //console.log(info);
    },
  },
};
</script>

