<template>
  <div class="RouterInfo">
    <h3>路由传参</h3>
    <p class="i"></p>
    <pre>{{
      `//配置页面路由
path: '/home/:name' --该路由地址必须传参匹配才能进入`
    }}</pre>
    <p>路由参数配置解析</p>
    <pre>{{
      `this.$route信息
      --fullPath 路由地址的所有地址信息/home/mine/张三?name=张三
      --params  动态路由(配置/home/:name/:age)匹配到的信息自动解析{name:'张三',age:18}
      --path  地址后的路由信息 /home/mine/张三
      --query   ?后面的内容自动解析  /home?name=张三&age=19   {name:'张三',age:19}

this.$router信息
      --routes：路由index.js页面那些配置信息`
    }}</pre>
    <h3>动态路由传参</h3>
    <p>可以通过 props:['name'] 来接收</p>
    <pre>{{
      `{
    path: '/home', 
    component: Home,
    props: { name: '张三' }
  },`
    }}</pre>
    <p>可以配置props:true 通过自动解析/:name变成 props:['name'] 来接收</p>
    <pre>{{
      `{
    //name 和 age 会自动解析成props接收
    path: '/home/:name/:age',
    component: Home, 
    props: true,
  },`
    }}</pre>
    <p class="i">
      刚刚那两种属性只能接收配置好的动态路由参数 , 如果想要接收?name
      则需要手动配置函数
    </p>
    <pre>
let fun = ({ params, query }) => {
  //默认传入route
  return {
    name: params.name,
    age: params.age,
    sex: query.sex
  }</pre
    >
    <pre>
{
    path: '/home/:name/:age',
    component: Home,
    props: fun,
  },</pre
    >
    <pre>props:['name','age','sex']</pre>
  </div>
</template>

<script>
export default {
 // props: ["name"],
  mounted() {
    console.log(this.$route);
  },
};
</script>

