<template>
  <div class="Vueconfig">
    <h3>vue.config.js 配置相关</h3>
    <p>配置文件新建在package.json同级</p>
    <div class="i">
      这里推荐几个链接:
      <a target="block" href="https://www.jianshu.com/p/b358a91bdf2d"
        >vue.config.js 配置</a
      >
      <a target="block" href="https://www.cnblogs.com/theblogs/p/10890362.html"
        >vue.config.js 配置</a
      >
      <a
        target="block"
        href="https://blog.csdn.net/m0_37820004/article/details/106896889"
        >vue.config.js 配置</a
      >
    </div>
    <h3>配置代理服务器-axios请求跨域</h3>
    <p></p>
    <pre>{{
      `module.exports = {
  publicPath: "./",
  devServer: {
    open: true,//设置npm run serve自动打开
    port: 1880,//设置本地端口
    host: 'localhost',//设置本地域名
    proxy: {
      //设置代理
      '/api': {
        //拦截 请求中包含/ajax的
        target: 'http://m.maoyan.com', //要跨域的域名
        changeOrigin: true, //是否开启跨域
        secure: false, //如果是http接口，需要配置该参数
        pathRewrite: {
          '^/api': '' //把/api替换成空
        }
      }
    }
  }
}`
    }}</pre>
    <pre>{{
      `axios.get("/api/ajax/movieOnInfoList?optimus_risk_level=71&optimus_code=10")
         .then((res) => {console.log(res)})
         .catch((err) =>{console.log(err)})`
    }}</pre>
    <h3>其他配置</h3>
    <p class="i">
      这个值也可以被设置为空字符串 ('') 或是相对路径
      ('./')，这样所有的资源都会被链接为相对路径，这样打出来的包可以被部署在任意路径。
    </p>
    <pre>{{
      `module.exports = {
  publicPath: "./", 
}`
    }}</pre>
    <div class="i">
      构建输出目录（打包位置） outputDir 当运行 npm run build
      时生成的生产环境构建文件的目录
    </div>
    <pre>
module.exports = {
 outputDir: 'dist',
}</pre
    >
    <div class="i">
      静态资源目录 assetsDir 放置生成的静态资源 (js、css、img、fonts) 的目录
    </div>
    <pre>
module.exports = {
 assetsDir: 'assets',
}</pre
    >
    <div class="i">
      eslint代码检测 是否开启eslint保存检测，有效值：ture | false | 'error'<br />
      设置为 true 时，eslint-loader 会将 lint <br />
      错误输出为编译警告。默认情况下，警告仅仅会被输出到命令行，且不会使得编译失败
      <br />
      希望让 lint 错误在开发时直接显示在浏览器中，可以使用
      lintOnSave:'error'。这会强制 eslint-loader 将 lint 错误输出为编译错误
    </div>
    <pre>
module.exports = {
 lintOnSave: false,
}</pre
    >
  </div>
</template>

<script>
export default {};
</script>

