<template>
  <div class="Slotdemo">
    <h3>插槽Slot</h3>
    <p>插槽的本质其实还是组件</p>
    <p>只是组件需要各种传递参数很不方便</p>
    <p>
      插槽就相当于组件内置了空位,使用组件时候插入相应的信息即可,数据可以直接操作
    </p>
    <pre>{{ info }}</pre>
  </div>
</template>

<script>
export default {
  data() {
    return {
      info: `
//slot标签，如果需要绑定指定插槽需要给name属性--直接在当前页面就能操作数据
<Footer>
  <p slot="name">我是插到name中的内容</p>
</Footer>

//组件中
<template>
 <div class="Slotdemo">
   <slot name="name"></slot> //里面将会插入数据
  </div>
</template>
    `,
    };
  },
};
</script>

