<template>
  <div class="Filtration">
    <h3>过滤器</h3>
    <p>有时候我们的数据格式并不是我们想要的,这时候可以使用过滤器,过滤数据</p>
    <h3>自定义全局过滤器</h3>
    <p>全局过滤器当然定义在全局下,比如main.js</p>
    <p>金钱过滤:{{ qian | money1 }}</p>
    <pre>{{ guolvdemo }}</pre>
    <h3>自定义局部过滤器</h3>
    <p>局部过滤器只在当前页有效</p>
    <p>金钱过滤:{{ qian | money2 }}</p>
    <pre>{{ jubudemo }}</pre>
    <h3>时间过滤器(moment插件)</h3>
    <p>npm install moment --save</p>
    <p>import moment from "moment"</p>
    <p>格式化日期:{{ time | timeformat }}</p>
    <p>格式化日期:{{ time | timeformat("HH:mm:ss") }}</p>
    <pre>{{ timeformat }}</pre>
    <h3>每秒更新时间</h3>
    <pre>{{ timesdemo }}</pre>
  </div>
</template>

<script>
import moment from "moment";
export default {
  data() {
    return {
      qian: 16854,
      guolvdemo: `
//main.js中

/*  自定义全局过滤器 金钱过滤  */
Vue.filter('money1', (value) => {
  return "￥" + Number(value).toFixed(2);
})

//html中
<p>金钱过滤:{{ qian | money }}</p>

//data中
 qian:16854`,
      jubudemo: `
 //局部过滤器 当前页钩子中
  filters: {
    money2(value) {
      return "￥" + Number(value).toFixed(2);
    },
  },

//html中
<p>金钱过滤:{{ qian | money2 }}</p>

//data中
qian: 16854`,
      time: new Date(),
      timeformat: `
//html
<p>格式化日期:{{ time | timeformat }}</p>
<p>格式化日期:{{ time | timeformat("HH:mm:ss") }}</p>

//data
time: new Date(),

//filters
timeformat(value, format = "YYYY-MM-DD HH:mm:ss") {
//传入的话还是format  如果没有传入格式就是这个格式
return moment(value).format(format);},
      `,
      timesdemo: `
//mounted
mounted() {
    this.timedemo = setInterval(() => {
      this.time = new Date();
    }, 1000);
  },

//记得清除定时器
destroyed() {
    clearInterval(this.timedemo);
  }`,
    };
  },
  methods: {},
  mounted() {
    this.timedemo = setInterval(() => {
      this.time = new Date();
    }, 1000);
  },
  destroyed() {
    clearInterval(this.timedemo);
  },
  computed: {},
  //局部过滤器
  filters: {
    money2(value) {
      return "￥" + Number(value).toFixed(2);
    },
    timeformat(value, format = "YYYY-MM-DD HH:mm:ss") {
      //传入的话还是format  如果没有传入格式就是这个格式
      return moment(value).format(format);
    },
  },
};
</script>

