<template>
  <div class="Pubsub">
    <h3>消息订阅与发布Pubsub</h3>
    <p>npm install --save pubsub-js</p>
    <p>import Pubsub from "pubsub-js"</p>
    <p>通常适合传递方法--多组件传递方法</p>
    <p>
      总的来说,就是一个函数中转站,引入后,可以提交函数,可以在mounted中展开函数
    </p>
    <pre>{{ info }}</pre>
  </div>
</template>

<script>
import Pubsub from "pubsub-js";
export default {
  data() {
    return {
      info: `
//提交申请,去触发函数
demo() {
      //提交函数--存入名delTodo 存入数据函数
      Pubsub.publish("delTodo", (fun = "123") => {
        return fun;
      });
    },

//收到函数触发请求,响应事件
  mounted() {
    Pubsub.subscribe("delTodo", (msg, token) => {
      //msg是你存入的数据名(delTode)
      //token是你传递参数携带的数据
      this.del(token); //调用函数
    });
  },
      `,
    };
  },
};
</script>

