import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    value: 0
  },
  mutations: {
    ADD(state) {
      state.value++;
    },
    DEL(state) {
      state.value--;
    },
    ADD_TWO(state, index) {
      state.value += index;
    },
    DEL_TWO(state, index) {
      state.value -= index;
    }
  },
  actions: {
    yibuadd({ commit }) {
      commit('ADD');
    },
    yibudel({ commit }) {
      commit('DEL');
    },
    yibuadd_two({ commit }, index) {
      commit('ADD_TWO', index)
    },
    yibudel_two({ commit, state }, index) {//state可以拿到但是只读
      commit('DEL_TWO', index)
    }
  },
  modules: {
  },
  getters: {
    value_type(state) {
      if (state.value % 2 != 0) {
        return '奇数'
      } else {
        return "偶数"
      }

    }
  },
})
