<template>
  <div class="Vuex_todulist">
    <h3>使用小案例</h3>
    <h3>第一种用法--直接当函数/变量用</h3>
    <p>我是state的数据:{{ $store.state.value }}</p>
    <button @click="$store.commit('ADD')">同步加一</button>
    <button @click="$store.commit('DEL')">同步减一</button>
    <hr />
    <button @click="$store.commit('ADD_TWO', 2)">同步加二</button>
    <button @click="$store.commit('DEL_TWO', 2)">同步减二</button>
    <hr />
    <button @click="$store.dispatch('yibuadd')">异步加一</button>
    <button @click="$store.dispatch('yibudel')">异步减一</button>
    <hr />
    <button @click="$store.dispatch('yibuadd_two', 2)">异步加二</button>
    <button @click="$store.dispatch('yibudel_two', 2)">异步减二</button>
    <pre>{{ storedemo1 }}</pre>
    <h3>第二种方法--展开使用</h3>
    <p>{{ value }}</p>
    <button @click="ADD">同步加一</button>
    <button @click="DEL">同步减一</button>
    <hr />
    <button @click="ADD_TWO(2)">同步加二</button>
    <button @click="DEL_TWO(2)">同步减二</button>
    <hr />
    <button @click="yibuadd">异步加一</button>
    <button @click="yibudel">异步减一</button>
    <hr />
    <button @click="yibuadd_two(2)">异步加二</button>
    <button @click="yibudel_two(2)">异步减二</button>
    <pre>{{ storedemo2 }}</pre>
    <h3>vuex仓库中</h3>
    <pre>{{ store1 }}</pre>
  </div>
</template>

<script>
import { mapState, mapMutations, mapActions, mapGetters } from "vuex";
export default {
  data() {
    return {
      storedemo1: `
<p>我是state的数据:{{ $store.state.value }}</p>
<button @click="$store.commit('ADD')">同步加一</button>
<button @click="$store.commit('DEL')">同步减一</button>
<hr />
<button @click="$store.commit('ADD_TWO', 2)">同步加二</button>
<button @click="$store.commit('DEL_TWO', 2)">同步减二</button>
<hr />
<button @click="$store.dispatch('yibuadd')">异步加一</button>
<button @click="$store.dispatch('yibudel')">异步减一</button>
<hr />
<button @click="$store.dispatch('yibuadd_two', 2)">异步加二</button>
<button @click="$store.dispatch('yibudel_two', 2)">异步减二</button>`,
      store1: `
state: {
    value: 0
  },
mutations: {
    ADD(state) {
      state.value++;
    },
    DEL(state) {
      state.value--;
    },
    ADD_TWO(state, index) {
      state.value += index;
    },
    DEL_TWO(state, index) {
      state.value -= index;
    }
  },
  actions: {
    yibuadd({ commit }) {
      commit('ADD');
    },
    yibudel({ commit }) {
      commit('DEL');
    },
    yibuadd_two({ commit }, index) {
      commit('ADD_TWO', index)
    },
    yibudel_two({ commit, state }, index) {//state可以拿到但是只读
      commit('DEL_TWO', index)
    }
  }`,
      storedemo2: `
//html中
<p>{{ value }}</p>
<button @click="ADD">同步加一</button>
<button @click="DEL">同步减一</button>
<hr />
<button @click="ADD_TWO(2)">同步加二</button>
<button @click="DEL_TWO(2)">同步减二</button>
<hr />
<button @click="yibuadd">异步加一</button>
<button @click="yibudel">异步减一</button>
<hr />
<button @click="yibuadd_two(2)">异步加二</button>
<button @click="yibudel_two(2)">异步减二</button>

//js中
import { mapState, mapMutations, mapActions } from "vuex";

computed: {
   ...mapState(["value"]),
},
  methods: {
...mapMutations(["ADD", "DEL", "ADD_TWO", "DEL_TWO"]),
...mapActions(["yibuadd", "yibudel", "yibuadd_two", "yibudel_two"]),
  }`,
    };
  },
  computed: {
    ...mapState(["value"]),
    ...mapGetters(["value_type"]),
  },
  methods: {
    ...mapMutations(["ADD", "DEL", "ADD_TWO", "DEL_TWO"]),
    ...mapActions(["yibuadd", "yibudel", "yibuadd_two", "yibudel_two"]),
  },
};
</script>

