<template>
  <div class="Vuexdemo">
    <h3>Vuex</h3>
    <p>
      之前那几种组件通信只能满足简单的要求,一旦项目复杂之后,通信变得无能为力
    </p>
    <p>vuex,相当于一个仓库,存放一些公用数据,需要用到就自己来取就好了</p>
    <p>虽然是一个仓库,但是仓库也有他的规则,下面是vuex的运行规则</p>
    <pre><img src="https://vuex.vuejs.org/vuex.png" /></pre>
    <h3>理解运行机制</h3>
    <p>State:存放数据的地方</p>
    <p>mutations:存放操作state的方法(同步)</p>
    <p>actions:存放多个方法（可以异步）</p>
    <p>getters:是vuex中的计算函数 页面中可以在计算函数中直接调用</p>
    <p>
      <strong class="i">分析:</strong
      >既然是数据通信,我当然能拿到State里的数据了.
      但是想要改变State内数据只能通过vuex中的mutations内的函数方法
    </p>
    <p>
      <strong class="i">分析:</strong
      >mutations可以拿到State并操作里面的数据,但是有一个条件,他只能是同步的请求,没办法处理异步
    </p>
    <p>
      <strong class="i">分析:</strong
      >异步请求只能走actions里面的函数,actions不能直接修改State内的数据,但是可以触发mutations内的函数修改
    </p>
  </div>
</template>

<script>
export default {};
</script>

