<template>
  <div class="Axiosdemo">
    <h3>axios的使用</h3>
    <p>import axios from "axios";</p>
    <p><a target="_block" href="https://axios-http.com/zh/docs/req_config">https://axios-http.com/zh/docs/req_config</a></p>
    <p>axios调用返回的数据就是Promise 可以通过then,catch使用</p>
    <button @click="getimg">小试牛刀</button>
    <pre>{{
      ` axios.get("http://demo.itlike.com/web/xlmc/api/homeApi/categoriesdetail/lk001")
           .then((res) => {console.log(res)})
           .catch((err) =>{console.log(err)})`
    }}</pre>
    <h3>简单使用</h3>
    <button @click="axiosdemo1">使用试试</button>
    <pre>{{
      ` axios({
        method:'get',
        url:'http://demo.itlike.com/web/xlmc/api/homeApi/categoriesdetail/lk001',
      }).then((res)=>{console.log(res);}).catch((err)=>{console.log(err);});`
    }}</pre>
    <h3>get传递参数</h3>
    <button @click="axiosdemo2">传递参数</button>
    <pre>{{ demo2 }}</pre>
    <h3>post传递参数</h3>
    <button @click="axiosdemo3">传递参数</button>
    <pre>{{ demo3 }}</pre>
    <h3>并发请求--等待多个请求结束后调用</h3>
    <p>同Promise.all 等待多个请求结束以后调用</p>
    <p>里面的axios不可以.then((res)=>{}) 否则没有结果</p>
    <button @click="axiosdemo4">等待多个接受后请求</button>
    <pre>{{demo4}}</pre>
  </div>
</template>

<script>
import axios from "axios";
export default {
  data() {
    return {
      demo2: `      //.get传
       axios.get("http://demo.itlike.com/web/xlmc/api/homeApi/categoriesdetail/lk001",{params:{name:'张三',age:18}})
         .then((res) => {console.log(res)})
         .catch((err) =>{console.log(err)});

      //函数对象传入
       axios({
       method:'get',
       url:'http://demo.itlike.com/web/xlmc/api/homeApi/categoriesdetail/lk001',
       params:{name:'lisa',age:18},
       }).then((res)=>{console.log(res);}).catch((err)=>{console.log(err);});`,
      demo3: `      //.data传数据
      axios
        .post(
          "http://demo.itlike.com/web/xlmc/api/homeApi/categoriesdetail/lk001",
          { data: { name: "张三", age: 18 } }
        )
        .then((res) => {
          console.log(res);
        })
        .catch((err) => {
          console.log(err);
        });
        
      //函数对象data传入
      axios({
        method: "post",
        url: "http://demo.itlike.com/web/xlmc/api/homeApi/categoriesdetail/lk001",
        data: { name: "lisa", age: 18 },
      })
        .then((res) => {
          console.log(res);
        })
        .catch((err) => {
          console.log(err);
        });`,
      demo4:`    var a =  axios.get("http://demo.itlike.com/web/xlmc/api/homeApi/categoriesdetail/lk001",{params:{name:'张三',age:18}});

    var b =  axios.get("http://demo.itlike.com/web/xlmc/api/homeApi/categoriesdetail/lk001",{params:{name:'张三',age:18}});

    var c =  axios.get("http://demo.itlike.com/web/xlmc/api/homeApi/categoriesdetail/lk001",{params:{name:'张三',age:18}});

    axios.all([a,b,c]).then((res)=>{console.log(res);}).catch((err)=>{console.log(err);})`
    };
  },
  methods: {
    getimg() {
      axios
        .get(
          "http://demo.itlike.com/web/xlmc/api/homeApi/categoriesdetail/lk001"
        )
        .then((res) => {
          console.log(res);
        })
        .catch((err) => {
          console.log(err);
        });
    },
    axiosdemo1() {
      axios({
        method: "get",
        url: "http://demo.itlike.com/web/xlmc/api/homeApi/categoriesdetail/lk001",
      })
        .then((res) => {
          console.log(res);
        })
        .catch((err) => {
          console.log(err);
        });
    },
    axiosdemo2() {
      //.get传
      axios
        .get(
          "http://demo.itlike.com/web/xlmc/api/homeApi/categoriesdetail/lk001",
          { params: { name: "张三", age: 18 } }
        )
        .then((res) => {
          console.log(res);
        })
        .catch((err) => {
          console.log(err);
        });
      //函数对象传入
      axios({
        method: "get",
        url: "http://demo.itlike.com/web/xlmc/api/homeApi/categoriesdetail/lk001",
        params: { name: "lisa", age: 18 },
      })
        .then((res) => {
          console.log(res);
        })
        .catch((err) => {
          console.log(err);
        });
    },
    axiosdemo3() {
      //.data传数据
      axios
        .post(
          "http://demo.itlike.com/web/xlmc/api/homeApi/categoriesdetail/lk001",
          { data: { name: "张三", age: 18 } }
        )
        .then((res) => {
          console.log(res);
        })
        .catch((err) => {
          console.log(err);
        });
      //函数对象data传入
      axios({
        method: "post",
        url: "http://demo.itlike.com/web/xlmc/api/homeApi/categoriesdetail/lk001",
        data: { name: "lisa", age: 18 },
      })
        .then((res) => {
          console.log(res);
        })
        .catch((err) => {
          console.log(err);
        });
    },
    axiosdemo4(){
    var a =  axios.get("http://demo.itlike.com/web/xlmc/api/homeApi/categoriesdetail/lk001",{params:{name:'张三',age:18}})

    var b =  axios.get("http://demo.itlike.com/web/xlmc/api/homeApi/categoriesdetail/lk001",{params:{name:'张三',age:18}});

    var c =  axios.get("http://demo.itlike.com/web/xlmc/api/homeApi/categoriesdetail/lk001",{params:{name:'张三',age:18}});

    axios.all([a,b,c]).then((res)=>{console.log(res);}).catch((err)=>{console.log(err);})
    },
  },
};
</script>

<style>
</style>