<template>
  <div class="Zidingyi">
    <h3>子组件传递父组件(自定义事件)</h3>
    <p>子组件只是打个报告，父组件专门监听这个报告</p>
    <p>事件还是在父组件里定义</p>
    <p>函数--父组件也可以拿到子组件携带的参数</p>
    <pre>{{ info }}</pre>
    <h3>另一种写法</h3>
    <pre>{{ info2 }}</pre>
  </div>
</template>

<script>
export default {
  data() {
    return {
      info: `
// 父组件  --  监听btnClick这个东西 用来触发函数btnClick_on
<Emit @btnClick="btnClick_on"></Emit>
btnClick_on(value,index) {
      alert("收到了子组件的消息" + value + index);
    },

//子组件  --  触发父组件
 <button @click="de('456')">按钮</button>
 de(index){
    this.$emit('btnClick',"123",index) //报告!!触发btnClick , 传递参数"123"
  }
      `,
      info2: `
// 父组件
<Emit ref='header'></Emit>
mounted(){
  this.$refs.header.$on('btnClick'this.btnClick_on);
},

 methods: {
    btnClick_on(value, index) {
      alert("收到了子组件的消息" + value + index);
    },
  }


//子组件  --  触发父组件
 <button @click="de('456')">按钮</button>
 de(index){
    this.$emit('btnClick',"123",index) //报告!!触发btnClick , 传递参数"123"
  }`,
    };
  },
};
</script>

