<template>
  <div class="Shengm">
    <h3>生命周期</h3>
    <p>可以理解为,一个页面从加载到销毁的整个过程</p>
    <p>首先看图</p>
    <img class="shengm" src="https://cn.vuejs.org/images/lifecycle.png" />
    <pre>
      //beforeCreate vue被创建后第一个生命周期钩子
      //开始解析编译,这个时候网页还没有解析出任何东西

      //created 
      //网页已经解析了一些基本数据,该钩子通常可以调接口了

      //beforeMount
      //已经把data初始化,但是还只是占位符 还没有被挂载到dom

      //mounted
      //页面基本全部完成的钩子 通常可以调接口和处理页面初始化了

      //beforeUpdate
      //页面元素发生改变之前调用的钩子

      //updated
      //页面元素发生改变后调用的钩子

      //beforeDestroy
      //页面销毁前执行钩子 通常用来清除定时器等

      //destroyed
      //页面销毁后 已经不受vue控制了,dom元素还存在
    </pre>
    <h3>效果演示</h3>
    <p>请刷打开控制台,刷新页面</p>
    <button v-if="isif" @click="isif = !isif">点我改变元素试试</button>
    <pre>{{ shengmdemo }}</pre>
  </div>
</template>

<script>
export default {
  data() {
    return {
      shengmdemo: `
data()  {
return {
    isif: true,
  };
},

  beforeCreate() {
    console.log("开始解析编译了!");
  },
  created() {
    console.log("已经解析了一些基本数据,可以调接口了");
  },
  beforeMount() {
    console.log("已经把data初始化,但是还只是占位符 还没有被挂载到dom");
  },
  mounted() {
    console.log("页面基本全部完成的钩子 通常可以调接口和处理页面初始化了");
  },
  beforeUpdate() {
    console.log("页面元素要发生改变了!");
  },
  updated() {
    console.log("页面元素已经改变了!");
  },
  beforeDestroy() {
    console.log("页面要被销毁了,通常来清除定时器");
  },
  destroyed() {
    console.log("页面销毁了!");
  },`,
      isif: true,
    };
  },
  beforeCreate() {
    console.log("开始解析编译了!");
  },
  created() {
    console.log("已经解析了一些基本数据,可以调接口了");
  },
  beforeMount() {
    console.log("已经把data初始化,但是还只是占位符 还没有被挂载到dom");
  },
  mounted() {
    console.log("页面基本全部完成的钩子 通常可以调接口和处理页面初始化了");
  },
  beforeUpdate() {
    console.log("页面元素要发生改变了!");
  },
  updated() {
    console.log("页面元素已经改变了!");
  },
  beforeDestroy() {
    console.log("页面要被销毁了,通常来清除定时器");
  },
  destroyed() {
    console.log("页面销毁了!");
  },
};
</script>

<style>
.shengm {
  margin-left: 200px;
  height: 1500px;
}
</style>