<template>
  <div class="Rests">
    <h3>ref操作元素</h3>
    <p ref="p">我是p标签</p>
    <button @click="p">点我看看</button>
    <pre>{{refdemo}}</pre>
  </div>
</template>

<script>
export default {
  data() {
    return {
      refdemo:`
//html中
<p ref="p">我是p标签</p>
<button @click="p">点我看看</button>

//data中
p() {
    //可以打印出来看看
    console.log(this.$refs.p);
    //可以像js中操作元素一样
    this.$refs.p.style.color = 'red';
}`,
    };
  },
  methods: {
    p() {
      //可以打印出来看看
      console.log(this.$refs.p);
      //可以像js中操作元素一样
      this.$refs.p.style.color = 'red';
    },
  },
};
</script>

<style>
</style>