<template>
  <div class="Definition">
    <h3>自定义指令</h3>
    <p>除了vue自带的if show等等指令之外 还可以自己定义指令实现一些功能</p>
    <h4>自定义全局指令</h4>
    <p>全局指令,整个工程都可以使用,需要定义在全局的文件,比如main.js</p>
    <p v-convert1="world"></p>
    <pre>{{ quanjudemo }}</pre>
    <h4>自定义局部指令</h4>
    <p>局部指令只作用于当前页</p>
    <p v-convert2="world"></p>
    <pre>{{ jubudemo }}</pre>
  </div>
</template>

<script>
export default {
  data() {
    return {
      world: "hello world",
      quanjudemo: `
//全局指令当然定义在全局中 比如main.js

Vue.config.productionTip = false
/*  自定义全局指令 大小写转换 */
// convert1--指令名 el当前指令作用的标签 binding 被操作元素的一些数据
Vue.directive('convert1', (el, binding) => {
  //console.log(el, binding);
  el.textContent = binding.value.toUpperCase();
});

//页面中标签直接使用就行了
<p v-convert1="world"></p>

//data中的数据
world: "hello world"`,
      jubudemo: `
//局部指令定义在当前页 钩子函数 directives

directives: {
    /* 自定义局部指令 */
    convert2(el, binding) {
      el.textContent = binding.value.toUpperCase();
    },
}

//html中
<p v-convert2="world"></p>

//data中
 world: "hello world" `,
    };
  },
  methods: {},
  computed: {},
  mounted() {},
  directives: {
    /* 自定义局部指令 */
    convert2(el, binding) {
      el.textContent = binding.value.toUpperCase();
    },
  },
};
</script>

