<template>
  <div class="Classdemo">
    <h3>v-bind</h3>
    <li>v-bind(简写 : )意为绑定元素 绑定的属性会被vue解析</li>
    <hr />
    <a :href="href_demo">我是被绑定的链接</a>
    <pre
      >{{ href }}
    </pre>
    <h3>绑定样式</h3>
    <p :class="one">我是被绑定的样式</p>
    <p :class="{ class1: isone }">我是被绑定的样式</p>
    <p :class="[one, 'class2']">我是被绑定的样式</p>
    <p :class="[{ class1: isone }, 'class2']">我是被绑定的样式</p>
    <pre>{{ bindclass }}</pre>
  </div>
</template>

<script>
export default {
  data() {
    return {
      href_demo: "http://www.baidu.com",
      href: `
//html中
<a :href="href_demo">我是被绑定的链接</a>

//还可以这样 绑定src解析表达式 name是data中的
<img :src="'/path/to/images/' + name">

//绑定style size是data中的数据
<div :style="{ fontSize: size + 'px' }"></div>
<div :style="[styleObjectA, styleObjectB]"></div>

//data中
href_demo:"http://www.baidu.com"`,
      one: "class1",
      isone: true,
      bindclass: `
//html中
//one会被解析
<p :class="one">我是被绑定的样式</p>

//这里的class1是样式 只是解析表达式是否为true绑定样式
//里面逗号可以接着写
<p :class="{ class1: isone }">我是被绑定的样式</p>

//数组里的也会被解析 想直接绑定样式引号就行了
<p :class="[one, 'class2']">我是被绑定的样式</p>

//数组里面可以穿插各种类型
<p :class="[{ class1: isone }, 'class2']">我是被绑定的样式</p>

//实际运用
<p :class="{class1 : 1==2 ? true : false}"></p>
<p :class="[1==2 ? one : 'class2']"></p>
    
//data中
one: "class1",
isone: true,

//style中
.class1 {
color: red;
}
.class2 {
  background-color: pink;
}
      `,
    };
  },
  methods: {},
  mounted() {},
  computed: {},
};
</script>

<style>
.class1 {
  color: red;
}
.class2 {
  background-color: pink;
}
</style>