    <h3>首先需要了解一下关键的vue目录结构</h3>
    <pre>
    ├── node_modules    这是一大堆插件包 不需要管他
    ├── public                                 
    │   ├── favicon.ico                   网站图标(ico格式)
    │   ├── index.html                    vue编译的根页面(一般不操作,只是修改或者引入东西)
    ├── src                             项目源码目录    
    │   ├── assets                          资源目录--放一些图片啊,字体啊,公用css啊等等都行
    │   │   └── images              
    │   │       └── logo.png                  
    │   ├── components                      组件目录
    │   │   └── title.vue
    │   ├── routes                          前端路由模块
    │   │   └── index.js
    │   ├── store                           vuex模块
    │   │   └── index.js
    │   └── views                           页面目录
    │       ├── hello.vue
    │       └── notfound.vue
    │   ├── main.js                         入口js文件
    │   ├── app.vue                         页面根组件
    ├── package.json                    npm包配置文件，里面定义了项目的npm脚本，依赖包等信息

    //其他的没说到学到后面自己了解一下即可
</pre
    >
    <h3>vue文件熟悉</h3>
    <p>
      为什么要先说流程呢?因为使用vuecli脚手架开发就不是单纯的html页面了,了解文件可以帮助理解
    </p>

    <ol>
      <li>
        脚手架开发可以使用大量的第三方包帮助开发.vscode中ctrl+～即可打开控制台
      </li>
      <li>vue的运行首先进入到app.vue页面中</li>
      <li>
        一个vue页面有三个部分,template相当于html部分,style是css部分,script是js部分
      </li>
      <li>一个vue页面template只能有一个</li>
    </ol>
