<template>
  <div class="Computers">
    <h3>计算属性(computed)</h3>
    <li>计算属性也相当于data中的变量,只是他需要计算,不能做到直接定义</li>
    <li>计算属性具有缓存--只要计算属性内的东西不改变,缓存内一直都是这个值</li>
    <li>
      数据是响应式的--计算属性内的东西一旦改变就会重新计算并重新渲染到页面
    </li>
    <li>计算属性内默认有两个方法--getter(读取)--setter(修改)</li>
    <li>
      通常计算属性写成 属性名(){} 形式,默认是get方法,如果需要set方法就写成
      属性名:{get(){},set(){}}
    </li>
    <hr />
    姓<input type="text" v-model="firstName" /><br />
    名<input type="text" v-model="lastName" /><br />
    你的姓名是:<input type="text" v-model="fullName" />
    <pre>{{ Computersdemo1 }}</pre>
    <h3>双向计算属性(set)</h3>
    姓<input type="text" v-model="firstName2" /><br />
    名<input type="text" v-model="lastName2" /><br />
    你的姓名是:<input type="text" v-model="fullName2" />
    <pre>{{ Computersdemo2 }}</pre>
    <h3>watch侦听器</h3>
    <li>侦听器顾名思义就是侦听某个数据 --一旦发生改变就作出一些事情</li>
    <li>计算属性只用于计算操作 而侦听器可以用来响应事件</li>
    姓<input type="text" v-model="firstName3" /><br />
    名<input type="text" v-model="lastName3" /><br />
    你的姓名是:<input type="text" v-model="fullName3" />
    <pre>{{ watchdemo }}</pre>
    <h3>深度监听</h3>
    <p>为什么需要深度监听?</p>
    <p>监听通常只能监听到变量/数组的改变</p>
    <p>如果是监听一个对象,包括对象内的属性,那就需要深度监听</p>
    <hr />
    <input type="text" v-model="obj.model1" />只监听对象: {{ obj.model }}
    <input type="text" v-model="obj2.model2" />深度监听对象: {{ obj.model }}
    <pre>{{ shenduwatch }}</pre>
  </div>
</template>

<script>
export default {
  data() {
    return {
      obj: {
        model1: "",
      },
      obj2: {
        model2: "",
      },
      firstName: "",
      lastName: "",
      firstName2: "",
      lastName2: "",
      firstName3: "",
      lastName3: "",
      fullName3: "",
      Computersdemo1: `
//html中
姓<input type="text" v-model="firstName" /><br />
名<input type="text" v-model="lastName" /><br />
你的姓名是:<input type="text" v-model="fullName" />

//data中
firstName: "",
lastName: "",

//computed中
computed: {
    fullName: {
      get() {
        return this.firstName + this.lastName;
      },
    },
}`,
      Computersdemo2: `
//html中
姓<input type="text" v-model="firstName2" /><br />
名<input type="text" v-model="lastName2" /><br />
你的姓名是:<input type="text" v-model="fullName2" />

//data中
firstName2: "",
lastName2: "",

//computed中
      fullName2: {
      get() {
        //判断 姓和名都是空的话 返回空值
        if (!this.firstName2 && !this.lastName2) return;
        //返回姓名 中间用 "--" 隔开
        return this.firstName2 + "--" + this.lastName2;
      },
      set(value) {
        //判断返回的字符是否有 -- 没有就把字符清空
        //如果有--的话,先从--把姓和名分割出来 再赋值
        if (value.indexOf("--") != -1) {
          this.firstName2 = value.split("--")[0];
          this.lastName2 = value.split("--")[1];
        } else {
          this.firstName2 = this.lastName2 = "";
        }
      },
    }`,
      watchdemo: `
//html中
姓<input type="text" v-model="firstName3" /><br />
名<input type="text" v-model="lastName3" /><br />
你的姓名是:<input type="text" v-model="fullName3" />

//data中
firstName3: "",
lastName3: "",
fullName3: "",

//watch中
      watch: {
    firstName3(value) {
      if (value) {
        this.fullName3 = value + "--" + this.lastName3;
      }
    },
    lastName3(value) {
      if (value) {
        this.fullName3 = this.firstName3 + "--" + value;
      }
    },
    fullName3(value) {
      if (value.indexOf("--") != -1) {
        this.firstName3 = value.split("--")[0];
        this.lastName3 = value.split("--")[1];
      } else {
        this.fullName3 = "";
        this.firstName3 = this.lastName3 = "";
      }
    },
  }`,
      shenduwatch: `
 //深度监听中有三个东西
      //1.handler:回调函数,监听变化时执行的函数
      //2.deep:值是true/false,确认是否开启深度监听
      //3.immediate:值是true/false确认是否以当前的初始值执行回调函数

<input type="text" v-model="obj.model1" />只监听对象: {{ obj.model }}
<input type="text" v-model="obj2.model2" />深度监听对象: {{ obj.model }}

obj: {
        model1: "",
      },
      obj2: {
        model2: "",
      },

obj(value) {
      console.log("普通监听对象", value.model1);
    },
    obj2: {
      deep: true,
      immediate: true, //是否刚进来初始化就加载
      handler(value) {
        console.log("深度监听对象", value.model2);
      },
    },
`,
    };
  },
  methods: {},
  computed: {
    fullName: {
      get() {
        return this.firstName + this.lastName;
      },
    },
    fullName2: {
      get() {
        if (!this.firstName2 && !this.lastName2) return;
        return this.firstName2 + "--" + this.lastName2;
      },
      set(value) {
        if (value.indexOf("--") != -1) {
          this.firstName2 = value.split("--")[0];
          this.lastName2 = value.split("--")[1];
        } else {
          this.firstName2 = this.lastName2 = "";
        }
      },
    },
  },
  watch: {
    firstName3(value) {
      if (value) {
        this.fullName3 = value + "--" + this.lastName3;
      }
    },
    lastName3(value) {
      if (value) {
        this.fullName3 = this.firstName3 + "--" + value;
      }
    },
    fullName3(value) {
      if (value.indexOf("--") != -1) {
        this.firstName3 = value.split("--")[0];
        this.lastName3 = value.split("--")[1];
      } else {
        this.fullName3 = "";
        this.firstName3 = this.lastName3 = "";
      }
    },

    obj(value) {
      console.log("普通监听对象", value.model1);
    },
    obj2: {
      //深度监听中有三个东西
      //1.handler:回调函数,监听变化时执行的函数
      //2.deep:值是true/false,确认是否开启深度监听
      //3.immediate:值是true/false确认是否以当前的初始值执行回调函数
      deep: true,
      immediate: true, //是否刚进来初始化就加载
      handler(value) {
        console.log("深度监听对象", value.model2);
      },
    },
  },
};
</script>

<style>
</style>