<template>
  <div class="Config">
    <h3>常用配置</h3>
    <pre>
axios.defaults.baseURL = 'http://demo.itlike.com/web/xlmc/api'  --默认请求地址(会自动和url拼接)

axios.defaults.timeout = 1000 --1000毫秒  请求超过1000毫秒自动断开

method --请求方式
  --method:'get',
  --method:'post',
url  --请求地址
  --url:'http://demo.itlike.com/web/xlmc/api/homeApi/categoriesdetail/lk001' 可以是完整路径
  --url:'/homeApi/categoriesdetail/lk001' 可以是baseURL拼接上的地址</pre
    >
    <h3>简单封装</h3>
    <button @click="ceshi">调用试试</button>
    <p>
      封装单独创建文件夹 src/http 里面有三个文件 --index.js(主要封装的配置文件)
      baseURL.js(默认拼接的url地址) api.js(api接口统一管理)
    </p>
    <span class="i">baseURL.js中</span>
    <pre>{{
      `export const baseURL = "http://demo.itlike.com/web/xlmc/api"`
    }}</pre>
    <span class="i">api.js中</span>
    <pre>{{
      `import http from './index'

// 首页
export const getHome = data => {
  return http('get', '/homeApi/categoriesdetail/lk001', data);
};`
    }}</pre>
    <span class="i">index.js中</span>
    <pre>{{
      `import axios from "axios";  //引入axios
import { baseURL } from './baseURL'  //引入baseURL

//配置常用参数
axios.defaults.baseURL = baseURL; //默认拼接的url
axios.defaults.timeout = 5000; //超过5秒自动断开
axios.defaults.headers.post['Content-Type'] = 'application/x-www-from-urlencoded' //默认post返回数据的类型 //配置下载文件或者其他编码时可能遇到

//配置拦截
//请求拦截 --常用来携带固定参数 如 token
axios.interceptors.request.use((config) => {
  config.token = '11111111111111111111111111';
  return config; //拦截以后添加了个token
}, (error) => {
  return Promise.error(error); //如果有错误的话 直接使用Promise.error抛出
})

//相应拦截  --常用来过滤数据
axios.interceptors.response.use((response) => {
  console.log('拿到的res', response);
  if (response.status == 500) { //状态码
    console.log('服务器遇到错误，无法完成请求');
  }
  return response.data; //过滤--返回响应数据的data
}, (error) => {
  return Promise.error(error); //如果有错误的话 直接使用Promise.error抛出
})

//手动配置封装
export default function http(type = 'GET', url, params = {}) {
  //接收 type url params  返回一个 Promise
  return new Promise((resolve, reject) => {
    let promise; //定义一个变量接收结果
    if (type.toUpperCase() === 'GET') {
      //走get请求
      promise = axios({
        method: 'get',
        url,
        params,
      })
    } else if (type.toUpperCase() === 'POST') {
      //走post请求
      promise = axios({
        method: 'post',
        url,
        data: params,
      })
    }
    //处理返回数据  成功的话走resolve 失败的话走catch
    promise.then((res) => { resolve(res) }).catch((err) => { reject(err) });
  })
}
`
    }}</pre>
    <span class="i">实际调用</span>
    <pre>{{
      `getHome({ name: "jay", age: 18 })
        .then((res) => {
          console.log(res);
        })
        .catch((err) => {
          console.log(err);
        });`
    }}</pre>
  </div>
</template>

<script>
import { getHome } from "../../http/api";
export default {
  methods: {
    ceshi() {
      getHome({ name: "jay", age: 18 })
        .then((res) => {
          console.log("实际上的res", res);
        })
        .catch((err) => {
          console.log(err);
        });
    },
  },
};
</script>

