<template>
  <div class="Promisedemo">
    <h3>Promise</h3>
    <p>什么是Promise?为什么需要Promise?</p>
    <p>一个网络请求返回的类型,理解为一个容器,一般用来封装异步操作</p>
    <p>
      当我们从网络请求中拿到数据往往要根据数据成功返回后接着做另一个请求,从而形成嵌套回调地狱
    </p>
    <p>Promise有两个出口,resolve(成功)reject(失败)</p>
    <p>resolve通过.then((data)=>{}) 输出</p>
    <p>reject通过.catch((err)=>{}) 输出</p>
    <pre>{{
      `
      //promiseshow: false

      new Promise((resolve, reject) => {
      if (this.promiseshow) {
        resolve("成功了");
      } else {
        reject("失败了");
      }
    })
      .then((data) => {
        console.log(data);
      })
      .catch((err) => {
        console.log(err);
      });`
    }}</pre>
    <p>可以链式调用,根据上一个结果的成功或者失败响应响应数据</p>
    <p>链式调用的环节只要一个出错的直接输出reject的err,不会接着调用</p>
    <pre>{{
      `new Promise((resolve, reject) => {
      if (this.promiseshow) {
        resolve("成功了1");
      } else {
        reject("失败了1");
      }
    }).then((data) => {
      console.log(data);
      new Promise((resolve, reject) => {
        if (this.promiseshow_two) {
          resolve("成功了2");
        } else {
          reject("失败了2");
        }
      }).then((data) => {
        console.log(data);
        new Promise((resolve, reject) => {
          if (this.promiseshow) {
            resolve("成功了3");
          } else {
            reject("失败了3");
          }
        })
          .then((data) => {
            console.log(data);
          })
          .catch((err) => {
            console.log(err);
          });
      });
    });`
    }}</pre>
    <p>Promise.all 等待多个请求结束以后调用</p>
    <pre>{{
      `var a = new Promise((resolve, reject) => {
      if (this.promiseshow) {
        resolve("成功了1");
      } else {
        reject("失败了1");
      }
    });
    var b = new Promise((resolve, reject) => {
      if (this.promiseshow) {
        resolve("成功了2");
      } else {
        reject("失败了2");
      }
    });
    var c = new Promise((resolve, reject) => {
      if (this.promiseshow) {
        resolve("成功了3");
      } else {
        reject("失败了3");
      }
    });
    Promise.all([a, b, c])
      //data是一个数组放了这三个的data 所以可以结构
      .then(([data1, data2, data3]) => {
        console.log(data1, data2, data3);
      })
      .catch((err) => {
        console.log(err);
      });`
    }}</pre>
  </div>
</template>

<script>
export default {
  data() {
    return {
      promiseshow: true,
      promiseshow_two: false,
    };
  },
  mounted() {},
};
</script>

