<template>
  <div class="Routerguard">
    <h3>路由守卫</h3>
    <p>通过路由守卫可以对每次刷新或进入路由界面进行权限验证</p>
    <h3>路由前置守卫</h3>
    <p class="i">router/index.js</p>
    <p>设置全局前置守卫,to,去哪,from从哪来,next是否放行</p>
    <p>
      判断是去登录页面的吗?,否则看看有没有token,有就放行,没有就带着地址去登录
    </p>
    <pre>{{
      `router.beforeEach((to, from, next) => {
  if (to.path !== '/about') {
    //判断是否登录
    if (window.token) {
      console.log('有token');
      next();
    } else {
      console.log('没有token');
      next('/about?redirect=' + to.path);//记录redirect目的是登录后可以给你跳转回原先需要访问的页面
    }
  } else {
    next();
  }
})`
    }}</pre>
    <p class="i">登录</p>
    <p>点击登录携带token,判断是否有携带地址,有就去,没有就去home</p>
    <pre>{{
      `token() {
      window.token = true;
      const redirect = this.$route.query.redirect;
      if (redirect) {
        //有回调
        this.$router.push(redirect);
      } else {
        //没有回调
        this.$router.replace("/home");
      }
    },`
    }}</pre>
    <h3>全局后置守卫</h3>
    <p></p>
    <pre>
router.afterEach((to, from) => {
  console.log('路由跳转结束');
});</pre
    >
    <h3>路由局部守卫</h3>
    <pre>
{
      path: '/foo',
      component: Foo,
      beforeEnter: (to, from, next) => {
        // ...
      }
    }</pre
    >
    <h3>组件内守卫(钩子)</h3>
    <p></p>
    <pre>beforeRouteEnter (to, from, next) {
    // 在渲染该组件的对应路由被 confirm 前调用
    // 不！能！获取组件实例 `this`
    // 因为当守卫执行前，组件实例还没被创建
  },
  beforeRouteUpdate (to, from, next) {
    // 在当前路由改变，但是该组件被复用时调用
    // 举例来说，对于一个带有动态参数的路径 /foo/:id，在 /foo/1 和 /foo/2 之间跳转的时候，
    // 由于会渲染同样的 Foo 组件，因此组件实例会被复用。而这个钩子就会在这个情况下被调用。
    // 可以访问组件实例 `this`
  },
  beforeRouteLeave (to, from, next) {
    // 导航离开该组件的对应路由时调用
    // 可以访问组件实例 `this`
  }</pre>
  </div>
</template>

<script>
export default {};
</script>

