<template>
  <div class="Routerdemo">
    <h3>常用配置</h3>
    <p>import router from './router'</p>
    <p>new Vue({ router, store, render: h => h(App) }).$mount('#app')</p>
    <hr />
    <p>总体来说就是根据当前浏览器访问的地址做出不同的页面响应</p>
    <p>文件路径src/router/index.js</p>
    <p>路径的切换需要在页面中有个窗口展示路由内容</p>
    <p>放入两个链接切换地址看看效果</p>
    <p>&lt;router-view>&lt;/router-view></p>
    <a style="margin-right: 1rem" href="./#/routerdemo/routerhome">第一个页面</a
    ><a href="./#/routerdemo/about">第二个页面</a>
    <router-view class="view"></router-view>
    <pre>{{
      `import Vue from 'vue' //引入vue
import VueRouter from 'vue-router' //引入vuerouter
import Home from '../views/Home.vue' //引入页面

Vue.use(VueRouter) //把vuerouter挂载到vue

const routes = [
  {
    path: '/', //路径
    component: Home //路径页面
  }
]

const router = new VueRouter({ //新建router,存储相关配置
  routes
})

export default router
`
    }}</pre>
    <p>动态改变路由(网页)地址</p>
    <router-link style="margin-right: 1rem" to="/routerdemo/routerhome"
      >网页一</router-link
    >
    <router-link to="/routerdemo/about">网页二</router-link>
    <router-view class="view"></router-view>
    <pre>{{ demo }}</pre>
    <h3>动态路由相关解析</h3>
    <p class="i">动态路由的标签本质上是渲染成了a标签,也可以改变渲染</p>
    <router-link to="/routerdemo/routerhome" tag="span"
      >我被渲染成了span标签</router-link
    >
    <pre>{{ demo2 }}</pre>
    <p class="i">
      路由标签会动态根据地址给当前选中路由标签添加一个类名router-link-exact-active也可以自行添加
      active-class="myclas"
    </p>
    <router-link
      active-class="myclas"
      style="margin-right: 1rem"
      to="/routerdemo/routerhome"
      >网页一</router-link
    >
    <router-link active-class="myclas" to="/routerdemo/about"
      >网页二</router-link
    >
    <router-view class="view"></router-view>
    <pre>{{ demo3 }}</pre>
    <p class="i">动态路由跳转可以携带参数</p>
    <router-link to="/routerdemo/routerhome/?张三">携带参数</router-link>
    <pre>
&lt;router-link to="/routerdemo/routerhome/?张三">携带参数&lt;/router-link></pre
    >
    <h3>编程式跳转</h3>
    <p>1.push</p>
    <pre>{{demo4}}</pre>
    <pre>{{demo5}}</pre>
    <p>2.replace</p>
    <pre>{{`跟 router.push 很像，唯一的不同就是，它不会添加新记录，而是替换掉当前的页面`}}</pre>
    <pre>{{demo6}}</pre>
    <p>go</p>
    <pre>{{demo7}}</pre>
    <p>其他了解知识 keep-alive</p>
  </div>
</template>

<script>
export default {
  data() {
    return {
      demo: `<router-link to="/routerdemo/routerhome">网页一</router-link>
<router-link to="/routerdemo/about">网页二</router-link>`,
      demo2: `<router-link to="/routerdemo/routerhome" tag="span">渲染成了span标签</router-link>`,
      demo3: `
<router-link active-class="myclas" style="margin-right: 1rem" to="/routerdemo/routerhome">网页一</router-link>
<router-link active-class="myclas" to="/routerdemo/about">网页二</router-link>
<router-view class="view"></router-view>`,
      demo4:`// 字符串
router.push('home')

// 对象
router.push({ path: 'home' })

// 命名的路由  --携带参数, 需要动态路由 /:userId 匹配
router.push({ name: 'user', params: { userId: '123' }})

// 带查询参数，变成 /register?plan=private
router.push({ path: 'register', query: { plan: 'private' }})`,
      demo5:`注意：如果提供了 path，params 会被忽略，上述例子中的 query 并不属于这种情况。取而代之的是下面例子的做法，
      你需要提供路由的 name 或手写完整的带有参数的 path：

const userId = '123'
router.push({ name: 'user', params: { userId }}) // -> /user/123
router.push({ path: /user/123 }) // -> /user/123
// 这里的 params 不生效
router.push({ path: '/user', params: { userId }}) // -> /user`,
      demo6:`声明式	
<router-link :to="..." replace>
编程式
router.replace(...)`,
    demo7:`这个方法的参数是一个整数，意思是在 history 记录中向前或者后退多少步，类似 window.history.go(n)。

例子

// 在浏览器记录中前进一步，等同于 history.forward()
router.go(1)

// 后退一步记录，等同于 history.back()
router.go(-1)

// 前进 3 步记录
router.go(3)

// 如果 history 记录不够用，那就默默地失败呗
router.go(-100)
router.go(100)`,
    };
  },
};
</script>

<style>
.view {
  color: green;
}
</style>