<template>
  <div class="Xuanran">
    <h3>条件渲染</h3>
    <h3>v-if</h3>
    <p v-if="1 + 1 == 2">我是if</p>
    <p v-else-if="1 + 1 == 3">我是else-if</p>
    <p v-else>我是else</p>
    <pre>
      {{ ifdemo }}
    </pre>
    <h3>v-show</h3>
    <p v-show="1 + 1 == 2">我是被渲染的标签</p>
    <pre>{{ showdemo }}</pre>
    <h3>v-if和v-show的区别</h3>
    <pre>
      //v-if从dom上就不对标签进行渲染了
      //v-show会渲染到页面上,但是隐藏了,display:none;

      //根据实际的应用场景选择适合的指令</pre
    >
    <h3>v-for的使用</h3>
    <p v-for="(item, index) in vfor" :key="item.id">
      序号：{{ index + 1 }} 姓名：{{ item.name }} 年龄：{{ item.age }}
    </p>
    <pre>{{ fordemo }}</pre>
    <h3>为什么绑定key?</h3>
    <pre>
      //有利于Vue尽可能高效地渲染元素
      //把元素独立 不易混淆</pre>
  </div>
</template>

<script>
export default {
  data() {
    return {
      ifdemo: `
<p v-if="1 + 1 == 2">我是if</p>
<p v-else-if="1 + 1 == 3">我是else-if</p>
<p v-else>我是else</p>`,
      showdemo: `
<p v-show="1 + 1 == 2">我是被渲染的标签</p>`,
      vfor: [
        {
          id: 1,
          name: "张三",
          age: 18,
        },
        {
          id: 2,
          name: "李四",
          age: 19,
        },
        {
          id: 3,
          name: "王五",
          age: 20,
        },
      ],
      fordemo: `
//vfor是数据 循环出item是内容 index是索引
<p v-for="(item, index) in vfor" :key="item.id">序号：{{ index + 1 }} 姓名：{{ item.name }} 年龄：{{ item.age }}</p>
//在p标签内文字使用遍历出的数据 循环出列表内容`,
      keydemo: ``,
    };
  },
  methods: {},
};
</script>

