<template>
  <div class="Vuex_getters">
    <h3>getters</h3>
    <p>Vuex-getters,相当于一个vuex中的计算属性,和普通计算属性一样用</p>
    <hr />
    <h3>第一种,直接调用</h3>
    <p>{{ $store.state.value }}</p>
    <p>{{ $store.getters.value_type }}</p>
    <button @click="$store.commit('ADD')">加一</button>
    <pre>{{ getters1 }}</pre>
    <h3>第二种,展开使用</h3>
    <p>{{ value }}</p>
    <p>{{ value_type }}</p>
    <button @click="ADD">加一</button>
    <pre>{{ getters2 }}</pre>
    <h3>vuex中</h3>
    <pre>{{ vuexzhong }}</pre>
  </div>
</template>

<script>
import { mapState, mapMutations, mapActions, mapGetters } from "vuex";
export default {
  data() {
    return {
      vuexzhong: `
state: {
    value: 0
},
mutations: {
 ADD(state) {
   state.value++;
 },
},
getters: {
    value_type(state) {
      if (state.value % 2 != 0) {
        return '奇数'
      } else {
        return "偶数"
      }

    }
  }`,
      getters1: `
<p>{{ $store.state.value }}</p>
<p>{{ $store.getters.value_type }}</p>
<button @click="$store.commit('ADD')">加一</button>`,
      getters2: `
//html中
<p>{{ value }}</p>
<p>{{ value_type }}</p>
<button @click="ADD">加一</button>

//
import { mapState, mapMutations, mapGetters } from "vuex";
 computed: {
    ...mapState(["value"]),
    ...mapGetters(["value_type"]),
  },
  methods: {
    ...mapMutations(["ADD"]),
  }`,
    };
  },
  computed: {
    ...mapState(["value"]),
    ...mapGetters(["value_type"]),
  },
  methods: {
    ...mapMutations(["ADD"]),
  },
};
</script>

