<template>
  <div class="init">
    <h3>熟悉结构</h3>
    <pre>
      <img src="../assets/demo.png" >
    </pre>
    <h3>基础标签</h3>
    <h3>双括号表达式</h3>
    <p>{{ name + 123 }}</p>
    <p>{{ name.toUpperCase() }}</p>
    <p>{{ 1 == 2 ? "是" : "否" }}</p>
    <pre>
//双括号中插入变量 可以做一些简单的处理
      {{ kuohao }}</pre
    >
    <h3>v-text</h3>
    <p v-text="name">1111111111111</p>
    <pre>
//渲染文本数据到标签
//标签内的东西会失效
      {{ text }}</pre
    >
    <h3>v-html</h3>
    <p v-html="html_demo">123123</p>
    <pre>
//渲染html标签数据
//标签内的东西会失效
{{ html }}</pre
    >
    <h3>v-pre</h3>
    <p v-pre>{{ name }}</p>
    <pre>
//给标签加v-pre 不再渲染标签内的数据，原样显示
      {{ pre }}
    </pre>
    <h3>v-cloak</h3>
    <p v-cloak>111</p>
    <pre>
//页面加载时渲染页面有一定的延迟
//在延迟期间内标签不会暂时出来
{{ cloak }}
</pre
    >
    <h3>v-once</h3>
    <p v-once>{{ name }}</p>
    <pre>
//v-once 只渲染一次标签
//后续data内容变化了也不会再重新渲染
{{once}}
    </pre>
  </div>
</template>
<script>
export default {
  data() {
    return {
      name: " 张三 hahah  ",
      kuohao: `
//html中
<p>{{ name + 123 }}</p>
<p>{{ name.toUpperCase() }}</p>
<p>{{ 1==2 ? "是" : "否" }}</p>

//data 中
name: " 张三 hahah  ",`,
      text: `
//html中
<p v-text="name">1111111111111</p>

//data 中
name: " 张三 hahah  "`,
      html_demo: "<p>我是被渲染的数据</p>",
      html: `
//html中
<p v-html="html">123123</p>

//data中
 html_demo:"<p>我是被渲染的数据</p>",
      `,
      pre: `
//html中
<p v-pre>{{name}}</p>

//data中
name: " 张三 hahah  "`,
      cloak: `
//html中
<p v-cloak>111</p>`,
      once: `
//html中
<p v-once>{{ name }}</p>

//data中
name: " 张三 hahah  "`,
    };
  },
  methods: {},
};
</script>

<style>
</style>