<template>
  <div class="Propsdemo">
    <h3>组件通信</h3>
    <p>每一个.vue文件都可以当做一个单独组件</p>
    <p>使用其他组件,有时需要吧数据传给子组件</p>
    <br />
    <h3>父组件传递子组件(props)</h3>
    <p>props传递时除了字符串类型其他都要动态绑定 ：</p>
    <pre>{{ fuzujian }}</pre>
    <p>子组件接收传入的数据</p>
    <pre>{{ zuzujian }}</pre>
    <h3>传入函数调用问题</h3>
    <p>父组件把函数传给子组件</p>
    <p>子组件调用的还是父组件定义的函数--直接携带参数即可</p>
    <pre>{{ hanshu }}</pre>
  </div>
</template>

<script>
export default {
  data() {
    return {
      fuzujian: `
//1.引入组件
import Props from "./components/props";
//2.html中使用组件 
<Props name="张三" :age="18" :p="obj" :onck="ck"></Props>
//3.定义数据传递给子组件
obj: {
        name: "张三",
        age: 18,
      },
ck(name, age) {
      alert("你好"+this.name+"，今年你"+this.age+"啦！");
    }`,
      zuzujian: `
//子组件通过这个钩子来接收数据
props: {
  //接收数据有很多种写法,比如
  ["name","age","p","onck"]

  //限制传入类型
  name:String,
  age:Number,
  p:Object,
  onck:Function

  //限制类型+是否必传+默认字段
  name: { type: String, required: false, default: "姓名" },
    age: { type: Number, required: false, default: 18 },
    p: {
      type: Object,
      required: false,
      default: () => {
        return { name: "张三", age: 18 };
      },
    },
    onck: {
      type: Function,
      required: false,
      default: () => {
        alert("默认");
        return 0
      },
    },
}
      `,
      hanshu: `
//父组件定义  --直接定义参数即可
ck(name, age) {
      alert("你好"+this.name+"，今年你"+this.age+"啦！");
    }

//子组件使用  --可以传入参数
<button @click="onck('李四', 19)">按钮</button>
      `,
    };
  },
};
</script>

