<template>
  <div class="Vmodel">
    <h1>v-model通常用于表单内容和数据的双向绑定</h1>
    <hr />
    <h3>文本框</h3>
    <input type="text" v-model="text" />
    <p>表单的内容是:{{ text }}</p>
    <pre>{{ textdemo }}</pre>
    <h3>复选框</h3>
    <input type="checkbox" id="jack" value="Jack" v-model="checkedNames" />
    <label for="jack">Jack</label>
    <input type="checkbox" id="john" value="John" v-model="checkedNames" />
    <label for="john">John</label>
    <input type="checkbox" id="mike" value="Mike" v-model="checkedNames" />
    <label for="mike">Mike</label><br /><span>{{ checkedNames }}</span
    ><br />
    <input type="checkbox" v-model="checkedNames2" />不绑定value按钮<br />{{
      checkedNames2
    }}
    <br />

    <pre>{{ fuxuank }}</pre>
    <h3>单选按钮</h3>
    <div id="example-4">
      <input type="radio" id="one" value="One" v-model="picked" />
      <label for="one">One</label>
      <br />
      <input type="radio" id="two" value="Two" v-model="picked" />
      <label for="two">Two</label>
      <br />
      <span>Picked: {{ picked }}</span>
      <pre>{{ radiodemo }}</pre>
    </div>
    <h3>下拉选择框</h3>
    <div id="example-5">
      <select v-model="selected">
        <option disabled value="">请选择</option>
        <option value="1">A</option>
        <option value="2">B</option>
        <option>C</option>
      </select>
      <span>Selected: {{ selected }}</span>
    </div>
    <pre>{{ selectdemo }}</pre>
    <h3>
      <strong class="o"
        ><a target="_block" href="https://cn.vuejs.org/v2/guide/forms.html">查看更多</a></strong
      >
    </h3>
    <pre>//了解一下就行了{{gengduo}}</pre>
  </div>
</template>

<script>
export default {
  data() {
    return {
      checkedNames2: "",
      text: "",
      textdemo: `
 //html中
<input type="text" v-model="text" />
<p>表单的内容是:{{ text }}</p>

//data中
text: ""`,
      checkedNames: [],
      fuxuank: `
 //html中  input和label绑定id优化点击  value是双向绑定的值
<input type="checkbox" id="jack" value="Jack" v-model="checkedNames" />
<label for="jack">Jack</label>
<input type="checkbox" id="john" value="John" v-model="checkedNames" />
<label for="john">John</label>
<input type="checkbox" id="mike" value="Mike" v-model="checkedNames" />
<label for="mike">Mike</label>
<br />
<span>{{ checkedNames }}</span>

<input type="checkbox" v-model="checkedNames2" />不绑定value按钮{{checkedNames2}

//data中
checkedNames:[],
checkedNames2:''`,
      picked: "",
      radiodemo: `
//html中 input和label绑定id优化点击 value是双向绑定的值
<div id="example-4">
<input type="radio" id="one" value="One" v-model="picked" />
<label for="one">One</label>
<br />
<input type="radio" id="two" value="Two" v-model="picked" />
<label for="two">Two</label>
<br />
<span>Picked: {{ picked }}</span>
</div>

//data中
picked:""`,
      selected: "",
      selectdemo: `
//html中 
<div id="example-5">
<select v-model="selected">
<option disabled value="">请选择</option>
<option value="1">A</option>
<option value="2">B</option>
<option>C</option>//没有value时绑定值
</select>
<span>Selected: {{ selected }}</span>
</div>

//data中
selected:''`,
      gengduo:`
<input
  type="checkbox"
  v-model="toggle"
  true-value="yes"
  false-value="no"
>

// 当选中时
vm.toggle === 'yes'
// 当没有选中时
vm.toggle === 'no'

<select v-model="selected">
    <!-- 内联对象字面量 -->
  <option v-bind:value="{ number: 123 }">123</option>
</select>

// 当选中时
typeof vm.selected // => 'object'
vm.selected.number // => 123
      `,
    };
  },
  methods: {},
  mounted() {},
  computed: {},
};
</script>

<style>
</style>