<template>
  <div class="RouterIndex">
    <h3>配置路由文件</h3>
    <p class="i">基础结构</p>
    <pre>{{
      `import Vue from 'vue' //引入vue
import VueRouter from 'vue-router' //引入vuerouter
import Home from '../views/Home.vue' //引入页面

Vue.use(VueRouter) //把vuerouter挂载到vue

const routes = [
  {
    path: '/', //路径
    component: Home //路径页面
  }
]

const router = new VueRouter({ //新建router,存储相关配置
  routes
})

export default router
`
    }}</pre>
    <h3>路由懒加载</h3>
    <p>默认加载路由时会吧所有页面全局加载进去,然后跳转</p>
    <p>配置路由懒加载只有用到的时候才加载进来</p>
    <pre>
const About = () => import('../views/about.vue');
{  
  path:'/about'
  component: About,
}

//或者

{
  path: '/home',
  component: () => import('../views/about.vue') 
}
</pre>
    <h3>路由重定向</h3>
    <p>redirect重定向</p>
    <pre>
{
    path: '/home', //路径
    name: 'home',
    component: Home //路径页面
  },
  //路由重定向
  {
    path: '/', //访问更目录
    redirect: '/home', //地址重定向到/home
  },
  {
    path: '*', //访问路由以外路径
    redirect: '/home', //地址重定向到/home
  },
  {
    path: '*', //访问路由以外路径
    redirect: '/home', //地址重定向到/home
  },
  {
    path: '*', //访问路由以外路径
    redirect: { name: 'home' }, //重定向到name为 home的地址 
  },
  {
    path: '*', //访问路由以外路径
    redirect: to => { return '/home' }, //重定向到/home
  },</pre
    >
    <h3>路由模式 区别</h3>
    <p>history / hash</p>
    <p style="margin-right: 1rem" class="i">
      hash --路由地址后面加上# --对seo优化不利
    </p>
    <p class="i">
      history --路由地址后面没有#
      --访问二级页面时刷新会出现404,需要和后端配合配置重定向到首页路由
    </p>
    <p>设置路由模式方式</p>
    <pre>
const router = new VueRouter({ //新建router,存储相关配置
  // mode: 'history', 
  mode: 'hash',
  routes
})</pre
    >
    <h3>二级路由</h3>
    <p>二级路由本质上还是路由的写法,只是多嵌套了个children</p>
    <pre>{{
      `//路由
      {
        path: "/routerdemo",
        component: Routerdemo,
        children: [
          {
            path: 'routerhome',//不用加/了
            component: Routerhome

          },
          {
            path: 'about',//不用加/了
            component: About
          },
          {
            path: '/home',//重定向的配置
            redirect: '/home/about',//
          }
        ]
      },`
    }}</pre>
   
  </div>
</template>

<script>
export default {};
</script>

<style>
</style>