<template>
  <div class="Anli">
    <h3>排序</h3>
    <dir @click="chufa">
      <button>默认</button><button>升序</button><button>降序</button>
    </dir>
    <hr />
    <h3>筛选</h3>
    搜索<input type="text" v-model="text_filter" /> <br /><br />
    增加 姓名: <input type="text" v-model="bind.name" /> 性别<select
      v-model="bind.sex"
    >
      <option value="男">男</option>
      <option value="女">女</option></select
    >年龄: <input type="text" v-model="bind.age" />手机:
    <input type="text" v-model="bind.phone" /><button @click="add">添加</button>
    <ul>
      <li v-for="(item, index) in filter_shuju" :key="'arr' + index">
        {{ index + 1 }}) 姓名: {{ item.name }} 性别: {{ item.sex }} 年龄:
        {{ item.age }} 电话:{{ item.phone }}
        <button @click="del(index)">删除</button>
        <button @click="set(index)">修改姓名</button>
      </li>
    </ul>
    <pre>{{ demo_html }}</pre>
    <pre>{{ demo_js_data }}</pre>
    <pre>{{ demo_js_com }}</pre>
  </div>
</template>

<script>
export default {
  data() {
    return {
      shuju: [
        {
          name: "张三",
          sex: "女",
          age: 18,
          phone: "18788888888",
        },
        {
          name: "李四",
          sex: "女",
          age: 19,
          phone: "18788888888",
        },
        {
          name: "王五",
          sex: "女",
          age: 17,
          phone: "18788888888",
        },
      ],
      text_filter: "",
      flag: 0,
      bind: {
        name: "",
        sex: "",
        age: null,
        phone: "",
      },
      demo_html: `
//html中
<h3>排序</h3>
    <dir @click="chufa">
      <button>默认</button><button>升序</button><button>降序</button>
    </dir>
    <hr />
    <h3>筛选</h3>
    搜索<input type="text" v-model="text_filter" /> <br /><br />
    增加 姓名: <input type="text" v-model="bind.name" /> 性别<select
      v-model="bind.sex"
    >
      <option value="男">男</option>
      <option value="女">女</option></select
    >年龄: <input type="text" v-model="bind.age" />手机:
    <input type="text" v-model="bind.phone" /><button @click="add">添加</button>
    <ul>
      <li v-for="(item, index) in filter_shuju" :key="'arr' + index">
        {{ index + 1 }}) 姓名: {{ item.name }} 性别: {{ item.sex }} 年龄:
        {{ item.age }} 电话:{{ item.phone }}
        <button @click="del(index)">删除</button>
        <button @click="set(index)">修改姓名</button>
      </li>
    </ul>
    <pre>{{ demo_html }}</pre>
    <pre>{{ demo_js_data }}</pre>
    <pre>{{ demo_js_com }}</pre>`,
      demo_js_data: `
//data中
shuju: [
        {
          name: "张三",
          sex: "女",
          age: 18,
          phone: "18788888888",
        },
        {
          name: "李四",
          sex: "女",
          age: 19,
          phone: "18788888888",
        },
        {
          name: "王五",
          sex: "女",
          age: 17,
          phone: "18788888888",
        },
      ],
      text_filter: "",
      flag: 0,
      bind: {
        name: "",
        sex: "",
        age: null,
        phone: "",
      },
`,
      demo_js_com: `
 computed: {
    filter_shuju() {
      // 1. 获取数据  克隆一份自己用
      let { shuju, text_filter, flag } = this;

      // 2. 定义最后return的数组 arr

      let arr = [...shuju]; //这里一定要延展,才能拿到准确的数组

      // 3. 过滤出搜索的数据
      if (text_filter.trim()) {
        //text_filter.trim() --取消前后空格 依然有值的话
        arr = shuju.filter((value) => {
          //返回 过滤出name中包含 搜索索引的对象
          return value.name.indexOf(text_filter) != -1;
        });
      }
      //4.排序 根据点击事件flag动态值决定返回排序方式
      if (flag) {
        //0就不操作,也就是默认数据
        //固定写法--a-b升序 b-a降序
        arr.sort((a, b) => {
          if (flag == 1) {
            return a.age - b.age;
          } else if (flag == 2) {
            return b.age - a.age;
          }
        });
      }
      //console.log(flag);
      return arr;
    },
  },
  methods: {
    chufa(e) {
      if (e.target.innerText == "默认") {
        this.flag = 0;
      } else if (e.target.innerText == "升序") {
        this.flag = 1;
      } else if (e.target.innerText == "降序") {
        this.flag = 2;
      }
    },
    add() {
      this.shuju.push(this.bind);
    },
    del(index) {
      this.shuju.splice(index, 1);
    },
    set(index) {
      this.shuju[index].name = prompt("请输入新的姓名");
    },
  },`,
    };
  },
  computed: {
    filter_shuju() {
      // 1. 获取数据  克隆一份自己用
      let { shuju, text_filter, flag } = this;

      // 2. 定义最后return的数组 arr

      let arr = [...shuju]; //这里一定要延展,才能拿到准确的数组

      // 3. 过滤出搜索的数据
      if (text_filter.trim()) {
        //text_filter.trim() --取消前后空格 依然有值的话
        arr = shuju.filter((value) => {
          //返回 过滤出name中包含 搜索索引的对象
          return value.name.indexOf(text_filter) != -1;
        });
      }
      //4.排序 根据点击事件flag动态值决定返回排序方式
      if (flag) {
        //0就不操作,也就是默认数据
        //固定写法--a-b升序 b-a降序
        arr.sort((a, b) => {
          if (flag == 1) {
            return a.age - b.age;
          } else if (flag == 2) {
            return b.age - a.age;
          }
        });
      }
      //console.log(flag);
      return arr;
    },
  },
  methods: {
    chufa(e) {
      if (e.target.innerText == "默认") {
        this.flag = 0;
      } else if (e.target.innerText == "升序") {
        this.flag = 1;
      } else if (e.target.innerText == "降序") {
        this.flag = 2;
      }
    },
    add() {
      this.shuju.push(this.bind);
    },
    del(index) {
      this.shuju.splice(index, 1);
    },
    set(index) {
      this.shuju[index].name = prompt("请输入新的姓名");
    },
  },
  watch: {},
};
</script>

<style>
ul {
  list-style: none;
}
</style>