<template>
  <div class="ClickDemo">
    <h3>事件处理</h3>
    <li>vue中绑定事件非常容易,直接给元素绑定事件即可</li>
    <hr />
    <p v-show="isShow">我被按钮控制显示隐藏了</p>
    <button @click="isShow = !isShow">我被绑定了事件</button>
    <pre>{{ clickdemo }}</pre>
    <h3>触发函数</h3>
    <p v-show="isShow2">我被按钮控制显示隐藏了</p>
    <button @click="fun()">点我会触发一个函数</button>
    <pre>{{ fundemo }}</pre>
    <h3>传递参数</h3>
    <button @click="fun2('你好啊')">点我触发传递参数</button>
    <pre>{{ fundemo2 }}</pre>
    <h3>默认传递参数</h3>
    <button @click="fun3($event, '你好啊')">点我传递</button>
    <pre>{{ fundemo3 }}</pre>
    <h3>常用事件</h3>
    <pre>
//vue中绑定事件不需要加on 直接@click这样
<img src="https://1578460233.github.io/vue-js2/img/on.0a6ff889.png"></pre>
    <h3>事件修饰符<strong class="o">(了解)</strong></h3>
    .stop .prevent .capture .self .once .passive
    <a href="https://cn.vuejs.org/v2/guide/events.html">更多内容</a>
    <pre>{{ xiushifu }}</pre>
  </div>
</template>

<script>
export default {
  data() {
    return {
      isShow: true,
      isShow2: true,
      clickdemo: ` 
//html中
<p v-show="isShow">我被按钮控制显示隐藏了</p>
<button @click="isShow = !isShow">我被绑定了事件</button>

//data中
isShow: true`,
      fundemo: `
//html中
<p v-show="isShow2">我被按钮控制显示隐藏了</p>
<button @click="fun()">点我会触发一个函数</button>

//data中
isShow2: true,

//methods中
methods: {
  fun() {
      this.isShow2 = !this.isShow2;
  }
}`,
      fundemo2: `
//html中
<button @click="fun2('你好啊')">点我触发传递参数</button>

//methods
methods: {
  fun2(hello) {
    alert(hello);
  },
}`,
      fundemo3: `
//html中
<button @click="fun3($event, '你好啊')">点我传递</button>

//methods中
fun(e, hello) {
  console.log(e);
  console.log(hello);
}`,
      xiushifu: `
<!-- 阻止单击事件继续传播 -->
<a v-on:click.stop="doThis"></a>

<!-- 提交事件不再重载页面 -->
<form v-on:submit.prevent="onSubmit"></form>

<!-- 修饰符可以串联 -->
<a v-on:click.stop.prevent="doThat"></a>

<!-- 只有修饰符 -->
<form v-on:submit.prevent></form>

<!-- 添加事件监听器时使用事件捕获模式 -->
<!-- 即内部元素触发的事件先在此处理，然后才交由内部元素进行处理 -->
<div v-on:click.capture="doThis">...</div>

<!-- 只当在 event.target 是当前元素自身时触发处理函数 -->
<!-- 即事件不是从内部元素触发的 -->
<div v-on:click.self="doThat">...</div>

<!-- 点击事件将只会触发一次 -->
<a v-on:click.once="doThis"></a>

<!-- 滚动事件的默认行为 (即滚动行为) 将会立即触发 -->
<!-- 而不会等待 onScroll 完成  -->
<!-- 这其中包含 event.preventDefault() 的情况 -->
<div v-on:scroll.passive="onScroll">...</div>

<!-- 只有在 key 是 Enter 时调用 vm.submit() -->
<input v-on:keyup.enter="submit">`,
    };
  },
  methods: {
    fun() {
      this.isShow2 = !this.isShow2;
    },
    fun2(hello) {
      alert(hello);
    },
    fun3(e, hello) {
      console.log(e);
      console.log(hello);
    },
  },
  mounted() {},
  computed: {},
};
</script>

<style>
</style>