import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../components/Home'
import Welcome from '../components/Welcome'
import Preface from '../components/Preface'
import Environ from '../components/Environ'
import Principle from '../components/Principle'
//
import Init from '../components/Init'
import Classdemo from '../components/Classdemo'
import Xuanran from '../components/Xuanran'
import ClickDemo from '../components/medium/ClickDemo.vue'
import Vmodel from '../components/medium/Vmodel.vue'
import Rests from '../components/medium/Rests.vue'
import Computers from '../components/medium/Computers.vue'
import Shengm from '../components/medium/Shengm.vue'
import Definition from '../components/medium/Definition.vue'
import Filtration from '../components/medium/Filtration.vue'
import Anli from '../components/medium/Anli.vue'
import Donghua from '../components/Donghua.vue'
import Propsdemo from '../components/medium/Propsdemo.vue'
import Zidingyi from '../components/medium/Zidingyi.vue'
import Pubsub from '../components/medium/Pubsub.vue'
import Middle from '../components/medium/Middle.vue'
import Slotdemo from '../components/medium/Slotdemo.vue'
import Vuexdemo from '../components/medium/Vuexdemo.vue'
import Vuexdemo_todu from '../components/medium/Vuexdemo_todu.vue'
import Vuex_todulist from '../components/advanced/Vuex_todulist.vue'
import Vuex_getters from '../components/advanced/Vuex_getters.vue'
import Vue_modules from '../components/advanced/Vue_modules.vue'
import Axiosdemo from '../components/advanced/Axiosdemo.vue'
import Promisedemo from '../components/advanced/Promisedemo.vue'
import Axiosconfig from '../components/advanced/Axiosconfig.vue'
import Routerdemo from '../components/router/Routerdemo.vue'

//路由demo
import Routerhome from '../components/router/routerhome.vue'
import About from '../components/router/about.vue'
//
import RouterIndex from '../components/router/RouterIndex.vue'
import RouterInfo from '../components/router/RouterInfo.vue'
import Routerguard from '../components/router/Routerguard.vue'
import Vueconfig from '../components/Vueconfig.vue'








Vue.use(VueRouter)

const routes = [
  {
    path: '',
    redirect: '/home'

  },
  {
    path: '/home',
    component: Home,
    children: [
      { path: "", redirect: '/welcome' },
      { path: "/welcome", component: Welcome },
      { path: "/preface", component: Preface },
      { path: "/environ", component: Environ },
      { path: "/principle", component: Principle },
      //
      { path: "/init", component: Init },
      { path: "/classdemo", component: Classdemo },
      { path: "/xuanran", component: Xuanran },
      { path: "/clickDemo", component: ClickDemo },
      { path: "/vmodel", component: Vmodel },
      { path: "/rests", component: Rests },
      { path: "/computers", component: Computers },
      { path: "/shengm", component: Shengm },
      { path: "/definition", component: Definition },
      { path: "/filtration", component: Filtration },
      { path: "/anli", component: Anli },
      { path: "/donghua", component: Donghua },
      { path: "/propsdemo", component: Propsdemo },
      { path: "/zidingyi", component: Zidingyi },
      { path: "/pubsub", component: Pubsub },
      { path: "/middle", component: Middle },
      { path: "/slotdemo", component: Slotdemo },
      { path: "/vuexdemo", component: Vuexdemo },
      { path: "/vuexdemo_todu", component: Vuexdemo_todu },
      { path: "/vuex_todulist", component: Vuex_todulist },
      { path: "/vuex_getters", component: Vuex_getters },
      { path: "/vue_modules", component: Vue_modules },
      { path: "/axiosdemo", component: Axiosdemo },
      { path: "/promisedemo", component: Promisedemo },
      { path: "/axiosconfig", component: Axiosconfig },
      //路由
      {
        path: "/routerdemo",
        component: Routerdemo,
        children: [
          {
            path: 'routerhome',//不用加/了
            component: Routerhome

          },
          {
            path: 'about',//不用加/了
            component: About
          },
          {
            path: '/home',//重定向的配置
            redirect: '/home/about',//
          }
        ]
      },
      //
      { path: "/routerIndex", component: RouterIndex },
      { path: "/routerInfo", component: RouterInfo },
      { path: "/routerguard", component: Routerguard },
      { path: "/vueconfig", component: Vueconfig },


    ]
  }
]

const router = new VueRouter({
  /* mode: 'history', */
  mode: 'hash',
  base: process.env.BASE_URL,
  routes
})

export default router
