<template>
  <div class="Vue_modules">
    <h3>vuex目录解析</h3>
    <p>
      实际项目开发之中,简单的单文件就不足够满足我们的需求,所以需要分模块管理
    </p>
    <h3>中小型项目目录结构</h3>
    <pre><img style="width:100%;height:100%;" src="@/assets/vuexdemo1.png" ></pre>
    <h3>大型项目目录结构</h3>
    <pre>
      <img style="width:100%;height:100%;" src="@/assets/vuexdemo2.png" >
    </pre>
    <p>补充</p>
    <pre>
add ({ state, commit, rootState }) {
      //rootState 模块内的数据拿到根节点数据的方式
      if ((state.value + rootState.value) % 2 === 1) {
        commit('ADD') 
      }
    }</pre
    >
    <pre>
默认情况下，模块内部的 action、mutation 和 getter 是注册在全局命名空间的
这样使得多个模块能够对同一 mutation 或 action 作出响应。</pre
    >
    <pre>
如果不想他们都注册在全局,可以添加namespaced: true的方式
使其成为带命名空间的模块。当模块被注册后，它的所有 getter、action 及 mutation 都会自动根据模块注册的路径调整命名</pre
    >
    <pre>
       getters: {
        isAdmin () { ... } // -> getters['account/isAdmin']
      },
      actions: {
        login () { ... } // -> dispatch('account/login')
      },
      mutations: {
        login () { ... } // -> commit('account/login')
      },
    </pre>
  </div>
</template>

<script>
export default {};
</script>

